import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import BindWechat from '@/components/BindWechat'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },

    {
      path: '/bindWechat',
      name: 'BindWechat',
      component: BindWechat
    }
  ]
})



